@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@100;200;300;400;500;600;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Rubik:wght@300;400;500;600&display=swap);

/* =========== google fonts =========== */

/* ======== base style ======= */

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body,
html {
  /* background: #0a071a; */
}

section,
footer {
  padding: 70px 0px;
}

h1,
h2 {
  font-size: 2rem;
  font-weight: 500;
  font-family: "Poppins", sans-serif;
  color: #f5b70a;
}

p {
  font-size: 1rem;
  color: #c4c4c4;
  line-height: 30px;
  font-family: "Rubik", sans-serif;
}

a {
  text-decoration: none;
  color: unset;
}

i {
  color: #fff;
}

.slick-prev:before,
.slick-next:before {
  color: black !important;
}

.header{width:100%;height:80px;line-height:80px;position:fixed;top:0;left:0;z-index:9;background:#eeedf2}.navigation{display:flex;align-items:center;justify-content:space-between}.logo h2{margin-bottom:0}.nav__list{display:flex;align-items:center;margin-bottom:0;list-style:none;grid-column-gap:2.7rem;column-gap:2.7rem}.nav__item a{color:#f76e11;cursor:pointer;transition:.3s}.nav__item a:hover{color:#f5b70a}.custom__search{display:flex;align-items:center;padding:10px 25px;height:35px;border-radius:50px;background:rgba(196,196,196,.1450980392)}.custom__search input{border:none;outline:none;background:rgba(0,0,0,0);color:#f76e11}.custom__search span i{cursor:pointer}.mobile__menu{display:none}.mobile__menu span i{font-size:1.3rem}.cart__icon{position:relative;z-index:11;width:40px;height:30px;border-radius:5px;background:#f5b70a;display:flex;align-items:center;justify-content:center}.badge{position:absolute;top:-2px;right:-3px;z-index:12;color:#000;font-size:.8rem;font-weight:700}@media only screen and (max-width: 992px){.nav__menu{position:fixed;top:0;left:0;width:100%;height:100%;z-index:99;background:rgba(10,7,26,.4941176471);display:none}.nav__list__wrapper{position:absolute;top:0;right:0;width:250px;height:100%;z-index:100;background:#eeedf2;flex-direction:column;box-shadow:5px 5px 15px -5px #f5b70a;justify-content:center !important;padding:0px 20px}.nav__list{flex-direction:column;padding:0}.nav__item a{font-size:.9rem}.header{line-height:40px;height:60px;padding:10px 0px}.custom__search{border:1px solid rgba(196,196,196,.1450980392);padding:5px 15px;font-size:.9rem;height:30px;margin-top:15px}.mobile__menu{display:block}.logo h2{font-size:1.5rem}.active__menu{display:block}}
.btn{padding:7px 25px;border-radius:5px;background:#f76e11;color:#fff;font-size:.9rem;margin-top:30px}.btn:hover{color:#fff}@media only screen and (max-width: 992px){.slider__content h2{font-size:1.5rem}.slider__content p{font-size:.8rem}.slider__content .btn{padding:5px 20px;font-size:.7rem}}@media only screen and (max-width: 767px){.slider__content{padding-left:0}.slick-prev,.slick-next{display:none !important}}
.single__product{background:#eeedf2;border-radius:10px;cursor:pointer;animation:animate 1s ease-in}.single__product:hover{box-shadow:5px 5px 10px -5px #f5b70a}.product__content{padding:0px 20px;padding-bottom:15px}.product__content h6{color:#f5b70a;text-align:center}.rating span i{color:#f76e11}.rating{margin-bottom:10px}.price{color:#000}.price span{color:#f5b70a;font-size:1.3rem;font-weight:500}.shopping__icon{width:30px;height:30px;border-radius:5px;background:#f76e11;display:flex;align-items:center;justify-content:center;cursor:pointer}@keyframes animate{from{transform:scale(0)}to{transform:scale(1)}}@media only screen and (max-width: 767px){.single__product{margin:2rem}.product__content h6{font-size:.9rem}.price{font-size:.8rem}.price span{font-size:.9rem}}
